/* CJK override */

@font-face {
    font-family: quote1;
    src: local("思源黑体");
    unicode-range: U+201c, U+201d;
}

@font-face {
    font-family: quote2;
    src: local("思源黑体 CN");
    unicode-range: U+201c, U+201d;
}

@font-face {
    font-family: quote3;
    src: local("Source Han Sans SC");
    unicode-range: U+201c, U+201d;
}

:root {
    --mono: var(--mono-font), var(--cjk-font);
    --general-font: quote1, quote2, quote3, var(--text-font), var(--cjk-font), var(--fallback-font);
    --display-font: quote1, quote2, quote3, var(--title-font), var(--cjk-font), var(--fallback-font);
}

html {
    font-size: 16px;
}

body, .file-list-item-summary {
    font-family: var(--general-font);
    font-feature-settings: var(--general-font-feature-settings);
    color: var(--text-color);
    line-height: 1.6;
}

#write {
    max-width: 860px;
    margin: 0 auto;
    padding: 30px;
    padding-bottom: 100px;
}

@media only screen and (min-width: 1400px) {
    #write {
        max-width: 1024px;
    }
}

@media only screen and (min-width: 1800px) {
    #write {
        max-width: 1200px;
    }
}

#write>ul:first-child, #write>ol:first-child {
    margin-top: 30px;
}

h1, h2, h3, h4, h5, h6 {
    position: relative;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-weight: bold;
    line-height: 1.4;
    cursor: text;
    font-family: var(--display-font)!important;
}

h1, h2, h3, h4 {
    color: var(--title-color);
    letter-spacing: -0.04rem;
}

h6 {
    margin-top: .6rem;
    margin-bottom: .2rem;
}

h1:hover a.anchor, h2:hover a.anchor, h3:hover a.anchor, h4:hover a.anchor, h5:hover a.anchor, h6:hover a.anchor {
    text-decoration: none;
}

h1 tt, h1 code, h2 tt, h2 code, h3 tt, h3 code, h4 tt, h4 code, h5 tt, h5 code, h6 tt, h6 code {
    font-size: inherit;
}

h1 {
    font-size: 2.25em;
    line-height: 1.2;
}

h2 {
    font-size: 1.75em;
    line-height: 1.225;
}

h3 {
    font-size: 1.5em;
    line-height: 1.43;
}

h4 {
    font-size: 1.25em;
}

h5 {
    font-size: 1em;
}

h6 {
    font-size: 1em;
    color: #777;
}

p, blockquote, ul, ol, dl, table {
    margin: 0.8em 0;
}

li>ol, li>ul {
    margin: 0 0;
}

h1 code, h2 code, h3 code, h4 code, h5 code, h6 code {
    border: inherit;
    background-color: inherit;
}

hr {
    height: 2px;
    padding: 0;
    margin: 16px 0;
    background-color: #e7e7e7;
    border: 0 none;
    overflow: hidden;
    box-sizing: content-box;
}

li p.first {
    display: inline-block;
}

ul, ol {
    padding-left: 30px;
}

ul:first-child, ol:first-child {
    margin-top: 0;
}

ul:last-child, ol:last-child {
    margin-bottom: 0;
}

blockquote {
    color: var(--text-color);
    border: 1px solid var(--border);
    background-color: var(--elevated-background);
    border-radius: 4px;
